import React, { useState } from "react";
import SaveData from "../data/SaveData";
import Select from "react-dropdown-select";
import axios from "axios";

const About = () => {

    const [request, setRequest] = useState({
        userName: ""
        ,artifactCd : 0
        ,artifactPart : ""
        ,mainOption : ""
        ,attack : 0
        ,attackPer : 0
        ,defense : 0
        ,defensePer : 0
        ,healthPoint : 0
        ,healthPointPer : 0
        ,elementMastery : 0
        ,elementCharge : 0
        ,criticalProbablity : 0
        ,criticalDamage : 0 
    })

    const [artifactCd , setArtifactCd] = useState(null)
    const [artipactPart ,setArtifactPart] = useState("")

    const onChangeInput = (e) => {
        setRequest({
            ...request,
            [e.target.id]: e.target.value,
        });
    };

    const SendArtifact = () => {
        setRequest({
            ...request
            ,artifactCd : artifactCd
           ,artipactPart : artipactPart 
        })

        axios.post('v1/artifact/register', request);
    }

    return (
    <div>
        <h1>   </h1>
        <div className="form-group">
            <label htmlFor="user"> </label>
            <input type="text" id="userName" name="userName" placeholder="  " onChange={onChangeInput}/>
            <label htmlFor="artifact_name">  </label>
            <Select id = "artifactCd" name="artifactCd" options = {SaveData.ArtifactCd} onChange={setArtifactCd}></Select>
            <label htmlFor="artifact_part">  </label>
            <Select id = "artifact_part" name="artifactPart" options = {SaveData.ArtifactPart} onChange={setArtifactPart}></Select>
            <label htmlFor="main_option"> </label>
            <input type="text" id="main_option" name="mainOption" placeholder="  " onChange={onChangeInput}/>
            <label htmlFor="attack"></label>
            <input type="number" id="attack" name="attack" placeholder=" " onChange={onChangeInput}/>
            <label htmlFor="attack_percent"> </label>
            <input type="number" id="attack_percent" name="attackPer" placeholder="  " onChange={onChangeInput}/>
            <label htmlFor="defense"></label>
            <input type="number" id="defense" name="defense" placeholder=" " onChange={onChangeInput}/>
            <label htmlFor="defense_percent"> </label>
            <input type="number" id="defense_percent" name="defensePer" placeholder="  " onChange={onChangeInput}/>
            <label htmlFor="health_point"></label>
            <input type="number" id="health_point" name="healthPoint" placeholder=" " onChange={onChangeInput}/>
            <label htmlFor="health_point_percent"> </label>
            <input type="number" id="health_point_percent" name="healthPointPer" placeholder="  " onChange={onChangeInput}/>
            <label htmlFor="element_mastery"> </label>
            <input type="number" id="element_mastery" name="elementMastery" placeholder="  " onChange={onChangeInput}/>
            <label htmlFor="element_charge">  </label>
            <input type="number" id="element_charge" name="elementCharge" placeholder="   " onChange={onChangeInput}/>
            <label htmlFor="critical_probability"> </label>
            <input type="number" id="critical_probability" name="criticalProbablity" placeholder="  " onChange={onChangeInput}/>
            <label htmlFor="critical_damage">  </label>
            <input type="number" id="critical_damage" name="criticalDamage" placeholder="  " onChange={onChangeInput}/>
        </div>
    <button id="saveButton" type="submit" onClick={SendArtifact}></button>
    </div>
    );
};

export default About;